import { GET_ORDERS, GET_ORDERS_STATUS, GET_ORDERS_TYPE, ORDER_DASHBOARD_COUNT, GET_ORDER_STATUS_REPORTS_PIE } from '../types';
import API from '../../api';

export function getOrders() {
    return async dispatch => {
        API.get(`/Order/GetOrdersList`).then(async (response) => {
            if (response.isSuccess === true) {
                dispatch({ type: GET_ORDERS, data: response.payload })
            }
        });
    }
}

export function getOrdersDashboardCount() {
    return async dispatch => {
        API.get(`/Order/GetOrdersOverviewTopCount`).then(async (response) => {
            if (response.isSuccess === true) {
                dispatch({ type: ORDER_DASHBOARD_COUNT, data: response.payload })
            }
        });
    }
}

